<template>
  <div id="waveAnimation" v-if="!hideWave">
    <svg
      class="waves"
      xmlns="http://www.w3.org/2000/svg"
      xmlns:xlink="http://www.w3.org/1999/xlink"
      viewBox="0 24 150 28"
      preserveAspectRatio="none"
      shape-rendering="auto"
    >
      <defs>
        <path
          id="gentle-wave"
          d="M-160 40c20 0 58-8 88-8s 58 8 88 8s 58-8 88-8s 58 8 88 8v-50h-352z"
        />
      </defs>
      <g class="parallax">
        <use
          xlink:href="#gentle-wave"
          x="48"
          y="0"
          fill="rgba(255,255,255,0.9)"
        />
      </g>
    </svg>
  </div>
</template>

<script>
export default {
  data() {
    return {
      hideWave: false,
    };
  },
  created() {
    this.$router.beforeEach((to, from, next) => {
      if (to.meta.hideWave) {
        this.hideWave = true;
      } else {
        this.hideWave = false;
      }
      next();
    });
  },
};
</script>

<style scoped>
.waves {
  width: 100%;
  height: 220px;
}
#waveAnimation {
  position: relative;
  margin-top: auto;
  height: 20vh;
}
.parallax > use {
  animation: move-forever 25s cubic-bezier(0.55, 0.5, 0.45, 0.5) infinite;
}
.parallax > use:nth-child(1) {
  animation-delay: -2s;
  animation-duration: 7s;
}
@keyframes move-forever {
  0% {
    transform: translate3d(-90px, 0, 0);
  }
  100% {
    transform: translate3d(85px, 0, 0);
  }
}
</style>
