<template>
  <div class="bubble-background">
    <div class="bubbles-container">
      <span
        v-for="bubble in bubbles"
        :key="bubble.id"
        :style="bubble.style"
        class="bubble"
      />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      bubbles: [],
    };
  },
  mounted() {
    this.createBubbles();
  },
  methods: {
    createBubbles() {
      setInterval(() => {
        const bubble = this.createBubble();
        this.bubbles.push(bubble);
        setTimeout(() => {
          this.removeBubble(bubble.id);
        }, 8000);
      }, 600);
    },
    createBubble() {
      const minSize = 10;
      const maxSize = 40;
      const size = Math.random() * (maxSize + 1 - minSize) + minSize;
      const left = Math.random() * window.innerWidth + "px";
      const style = {
        width: `${size}px`,
        height: `${size}px`,
        left: left,
      };
      return { id: Date.now(), style: style };
    },
    removeBubble(id) {
      this.bubbles = this.bubbles.filter((bubble) => bubble.id !== id);
    },
  },
};
</script>

<style scoped>
.bubble-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.bubbles-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}
.bubble {
  position: absolute;
  bottom: -50px;
  background-color: transparent;
  border-radius: 50%;
  pointer-events: none;
  box-shadow: inset 0 0 25px rgba(255, 255, 255, 0.5);
  animation: bubble 8s linear infinite;
  z-index: 1;
}
.bubble::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  transform: scale(0.25) translate(-70%, -70%);
  background: radial-gradient(rgba(255, 255, 255, 0.1), transparent);
  border-radius: 50%;
}
@keyframes bubble {
  0% {
    transform: translateY(0);
    opacity: 1;
  }
  99% {
    opacity: 1;
  }
  100% {
    transform: translateY(-100vh);
    opacity: 0;
  }
}
</style>
