<template>
  <header v-if="!hideHeader">
    <div class="logo">
      <router-link to="/">
        <img alt="RIZMIC" src="../assets/logo.svg" />
      </router-link>
    </div>
    <nav>
      <div class="nav-right">
        <ul>
          <li><router-link to="/">ホーム</router-link></li>
          <li><router-link to="/about">自己紹介</router-link></li>
          <li><router-link to="/skills">スキル</router-link></li>
          <li><router-link to="/works">制作事例</router-link></li>
          <li><router-link to="/contact">連絡先</router-link></li>
        </ul>
      </div>
    </nav>
  </header>
</template>

<script>
export default {
  data() {
    return {
      hideHeader: false,
    };
  },
  created() {
    this.$router.beforeEach((to, from, next) => {
      if (to.meta.hideHeader) {
        this.hideHeader = true;
      } else {
        this.hideHeader = false;
      }
      next();
    });
  },
};
</script>

<style scoped>
header {
  padding: 1% 2%;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 3;
}
nav ul {
  display: flex;
}
nav li {
  font-size: 16px;
}
nav li:not(:first-child) {
  margin-left: 40px;
}
nav a {
  color: #2e0e02;
}
nav a:hover {
  opacity: 0.5;
}
nav a.router-link-exact-active {
  color: #578fc7;
}
.logo img {
  width: 180px;
}
</style>
